<template>
  <div class="main quality">
    <div class="main-search">
      <div class="search-item">
        <span class="item-left">开始日期</span>
        <span class="item-right">
          <el-date-picker
            v-model="search.beginCalldate"
            type="date"
            value-format="yyyy-MM-dd"
            placeholder="选择日期"
            @change="getSbeginTime">
         </el-date-picker>
        </span>
      </div>
      <div class="search-item">
        <span class="item-left">结束日期</span>
        <span class="item-right">
            <el-date-picker
              v-model="search.endCalldate"
              type="date"
              value-format="yyyy-MM-dd"
              placeholder="选择日期"
              :picker-options="endDatePicker"
              @change="getSendTime">
         </el-date-picker>
        </span>
      </div>
      <div class="search-item">
        <span class="item-left">开始时段</span>
        <el-select v-model="search.beginCallhour">
          <el-option label="08" value="" style="padding-left: 15px">08</el-option>
          <el-option label="09" value="09" style="padding-left: 15px">09</el-option>
          <el-option label="10" value="10" style="padding-left: 15px">10</el-option>
          <el-option label="11" value="11" style="padding-left: 15px">11</el-option>
          <el-option label="12" value="12" style="padding-left: 15px">12</el-option>
          <el-option label="13" value="13" style="padding-left: 15px">13</el-option>
          <el-option label="14" value="14" style="padding-left: 15px">14</el-option>
          <el-option label="15" value="15" style="padding-left: 15px">15</el-option>
          <el-option label="16" value="16" style="padding-left: 15px">16</el-option>
          <el-option label="17" value="17" style="padding-left: 15px">17</el-option>
          <el-option label="18" value="18" style="padding-left: 15px">18</el-option>
          <el-option label="19" value="19" style="padding-left: 15px">19</el-option>
          <el-option label="20" value="20" style="padding-left: 15px">20</el-option>
          <el-option label="21" value="21" style="padding-left: 15px">21</el-option>
          <el-option label="22" value="22" style="padding-left: 15px">22</el-option>
        </el-select>
      </div>
      <div class="search-item">
        <span class="item-left">结束时段</span>
        <el-select v-model="search.endCallhour">
          <el-option label="08" value="" style="padding-left: 15px">08</el-option>
          <el-option label="09" value="09" style="padding-left: 15px">09</el-option>
          <el-option label="10" value="10" style="padding-left: 15px">10</el-option>
          <el-option label="11" value="11" style="padding-left: 15px">11</el-option>
          <el-option label="12" value="12" style="padding-left: 15px">12</el-option>
          <el-option label="13" value="13" style="padding-left: 15px">13</el-option>
          <el-option label="14" value="14" style="padding-left: 15px">14</el-option>
          <el-option label="15" value="15" style="padding-left: 15px">15</el-option>
          <el-option label="16" value="16" style="padding-left: 15px">16</el-option>
          <el-option label="17" value="17" style="padding-left: 15px">17</el-option>
          <el-option label="18" value="18" style="padding-left: 15px">18</el-option>
          <el-option label="19" value="19" style="padding-left: 15px">19</el-option>
          <el-option label="20" value="20" style="padding-left: 15px">20</el-option>
          <el-option label="21" value="21" style="padding-left: 15px">21</el-option>
          <el-option label="22" value="22" style="padding-left: 15px">22</el-option>
        </el-select>
      </div>
      <div class="search-item">
        <span class="item-left">外呼团队</span>
        <span class="item-right">
           <el-select v-model="search.cid" multiple collapse-tags @change="changeStaff('corp')" >
              <template v-for="item in teamData">
                  <el-option :label="item.memo" :value="item.id"  :key="item.id">{{item.memo}}</el-option>
              </template>
           </el-select>
        </span>
      </div>
      <div class="search-item">
        <span class="item-left">品牌</span>
        <span class="item-right">
          <el-select  v-model="search.prjid_local" multiple collapse-tags @change="changeStaff('proj')">
              <template v-for="item in projData">
                  <el-option :label="item.memo" :value="item.id"  :key="item.id">{{item.memo}}</el-option>
              </template>
           </el-select>
        </span>
      </div>
      <div class="search-item">
        <span class="item-left">省份</span>
        <span class="item-right">
          <el-input placeholder="点击选择"   v-model="search.province" @click.native="dialog_batch('province')"></el-input>
        </span>
      </div>
      <div class="search-item">
        <span class="item-left">城市</span>
        <span class="item-right">
          <el-input placeholder="点击选择"  v-model="search.city" @click.native="dialog_batch('city')"></el-input>
        </span>
      </div>
      <div class="search-item">
        <span class="item-left">数据批次</span>
        <span class="item-right">
          <el-input placeholder="点击选择" readonly style="cursor: pointer" v-model="search.modelname" @click.native="dialog_batch('modelname')"></el-input>
        </span>
      </div>
      <div class="search-item">
        <span class="item-left">坐席姓名</span>
        <span class="item-right">
          <el-input placeholder="点击选择"  v-model="search.umemo" @click.native="dialog_batch('user')"></el-input>
        </span>
      </div>
      <div class="search-item" style="margin-top: 10px">
        <span class="item-left">分组条件</span>
        <span class="item-right">
            <el-checkbox label="日期" v-model="checked.calldate" @change="checkedChange('datetime', checked.calldate)" name="test"></el-checkbox>
            <el-checkbox label="时段" v-model="checked.callhour" @change="checkedChange('datehour', checked.callhour)" name="test"></el-checkbox>
            <el-checkbox label="省份" v-model="checked.province" @change="checkedChange('province', checked.province)" name="test"></el-checkbox>
            <el-checkbox label="城市" v-model="checked.city" @change="checkedChange('city', checked.city)" name="test"></el-checkbox>
            <el-checkbox label="外呼团队" v-model="checked.cid" @change="checkedChange('cid', checked.cid)" name="test"></el-checkbox>
            <el-checkbox label="坐席" v-model="checked.umemo" @change="checkedChange('uid', checked.umemo)" name="test"></el-checkbox>
            <el-checkbox label="品牌" v-model="checked.prjid_local" @change="checkedChange('prjid_local', checked.prjid_local)" name="test"></el-checkbox>
            <el-checkbox label="数据批次" v-model="checked.modelname" @change="checkedChange('modelname', checked.modelname)" name="test"></el-checkbox>
        </span>
      </div>
      <button type="button" class="btn-search" @click="search_detail">查询</button>
    </div>
    <div class="main-table">
      <div class="table-msg">
        <div class="msg-left">
          <p>查询符合上述条件的数据<span>{{list.totalCount}}</span>条</p>
        </div>
      </div>
      <el-table
        :data="list.data"
        style="width: 100%"
        border
        :default-sort = "{prop: 'date', order: 'descending'}"
      >
        <el-table-column
          v-if="checked.calldate"
          prop="datetime"
          align="center"
          :formatter="formatDate"
          label="日期"
          width="100">
        </el-table-column>
        <el-table-column
          v-if="checked.callhour"
          prop="datehour"
          align="center"
          label="时段">
        </el-table-column>
        <el-table-column
          v-if="checked.cid"
          prop="cid"
          align="center"
          label="外呼团队"
          :formatter="formatCorp">
        </el-table-column>
        <el-table-column
          v-if="checked.umemo"
          prop="user"
          align="center"
          :formatter="formatUser"
          label="坐席">
        </el-table-column>
        <el-table-column
          v-if="checked.prjid_local"
          prop="porj"
          align="center"
          :formatter="formatProj"
          label="品牌">
        </el-table-column>
        <el-table-column
          v-if="checked.modelname"
          prop="modelName"
          :formatter="formatModel"
          align="center"
          label="数据批次">
        </el-table-column>
        <el-table-column
          v-if="checked.province"
          prop="province"
          align="center"
          :formatter="formatProvince"
          label="省份">
        </el-table-column>
        <el-table-column
          v-if="checked.city"
          prop="city"
          align="center"
          :formatter="formatCity"
          label="城市">
        </el-table-column>
        <el-table-column
          prop="refusereason"
          align="center"
          label="不成单原因">
        </el-table-column>
        <el-table-column
          prop="count"
          align="center"
          label="占比">
        </el-table-column>\
      </el-table>
      <page-table show_size="true" v-bind:cur="search.page" v-bind:total_page="list.totalPage" v-bind:total_count="list.totalCount" @change="change_data"></page-table>
    </div>
    <el-dialog :visible.sync="popup_show">
      <popup-box :dataList='dataType' :dataId = 'dataId' ref="getDataName"></popup-box>
      <div slot="footer" class="dialog-footer">
        <el-button @click="popup_show=false">取消</el-button>
        <el-button type="primary" @click="save">保存</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import {format} from '@/config/util'
  import pageTable from '@/components/pager'
  import popupBox from '@/components/popup'
  import ajax from '@/config/ajax'
  import Qs from 'qs'
  export default {
    name: 'no_single_cause',
    components:{popupBox,pageTable},
    data () {
      return {
        search: {
          beginCalldate:  new Date(), // 开始时间
          endCalldate: new Date(), // 结束时间
          beginCallhour: '08', // 开始时段
          endCallhour: '22', // 结束时段
          cid: [],     // 外呼团队
          prjid_local: [], // 品牌
          province: '', // 省份
          provincevalue: '0',  //省份id
          city: '', // 城市
          cityvalue: '0',  //城市id
          modelname: '',     // 数据批次
          modelvalue: '0', //数据批次id
          umemo: '', // 坐席姓名
          uservalue: '0',
          groupBy: 'datetime,cid,prjid_local,uid,modelname', // 省份
          size: 10, // 每页数量
          page: 1 // 页码
        },
        list: {
          data: [], // 数据集
          totalPage: 0, // 总页数
          totalCount: 0 // 总条数
        },
        checked: {
          calldate: true,
          callhour: false,
          province: false,
          city: false,
          cid: true,
          umemo: true,
          prjid_local: true,
          modelname: true
        },
        popup_show: false,
        isCorpContainAll: true,     //外呼团队是否选则了全选
        isProjContainAll: true,     //品牌是否选则了全选
        endDatePicker: this.processDate(),
        teamData: [],  //外呼团队字典数组
        projData: [],  //品牌字典数组
        modelData: [], //数据批次字典数组
        provinceData: [],  //省份字典数组
        cityData: [],  //城市字典数组
        userData: [], //坐席姓名字典数组
        dataType: [],  //数据批次、省份、城市数据
        dataId: "",    //数据批次、省份、城市数据id分别
      }
    },
    created(){
      this.teamData = JSON.parse(localStorage.getItem("cropid"));
      this.projData = JSON.parse(localStorage.getItem("projid"));
      this.modelData = JSON.parse(localStorage.getItem("model"));
      this.provinceData = JSON.parse(localStorage.getItem("province"));
      this.cityData = JSON.parse(localStorage.getItem("city"));
      this.userData = JSON.parse(localStorage.getItem("uid"));
      this.search.cid[0]=this.teamData[0].id;
      this.search.prjid_local[0]=this.projData[0].id;
      this.getSeatList();
    },
    methods: {
      // 查询
      search_detail () {
        if(this.search.cid.length == 0){
          this.$message({
            message: '请选择外呼团队',
            type: 'warning'
          });
          return
        }else if(this.search.prjid_local.length == 0){
          this.$message({
            message: '请选择品牌',
            type: 'warning'
          });
          return
        }
        this.search.page = 1;
        this.getSeatList()
      },
      // 时间选择器开始时间
      getSbeginTime (val) {
        this.search.beginCalldate = new Date(val)
      },
      // 时间选择器结束时间
      getSendTime (val) {
        this.search.endCalldate = new Date(val)
      },
      processDate(){
        const  self = this;
        return {
          disabledDate(time) {
            if (self.search.beginTime) {  //如果开始时间不为空，则结束时间大于开始时间
              return new Date(self.search.beginTime).getTime() > time.getTime()
            } else {
              // return time.getTime() > Date.now()//开始时间不选时，结束时间最大值小于等于当天
            }
          }
        }
      },
      //格式化日期
      formatDate(row){
        if(row.datetime){
          var calldate = row.datetime.toString();
          return row.datetime = calldate.replace(/^(\d{4})(\d{2})(\d{2})$/, "$1-$2-$3");
        }
        
      },
      //品牌及外呼团队多选
      changeStaff(type){
        if(type == 'corp'){
          if(this.isCorpContainAll){
            this.isCorpContainAll = false;   // 如果之前选中了全部，则变化后肯定不包含全部了
            this.search.cid.splice(0, 1);// 则删除第一个全部
          }else{
            this.isCorpContainAll = this.search.cid.some(value => value === 0)
            if(this.isCorpContainAll){
              this.search.cid = [0];
            }else{
              if(this.search.cid.length == this.teamData.length-1){
                this.search.cid = [0];
                this.isCorpContainAll = true;
              }
            }
          }
        }else if(type == 'proj'){
          if(this.isProjContainAll){
            this.isProjContainAll = false;   // 如果之前选中了全部，则变化后肯定不包含全部了
            this.search.prjid_local.splice(0, 1);// 则删除第一个全部
          }else{
            this.isProjContainAll = this.search.prjid_local.some(value => value === 0)
            if(this.isProjContainAll){
              this.search.prjid_local = [0];
            }else{
              if(this.search.prjid_local.length == this.projData.length-1){
                this.search.prjid_local = [0];
                this.isProjContainAll = true;
              }
            }
          }
        }
      },
      // 获取初始化列表
      getSeatList () {
        var params = {};
        params.beginCalldate = format(this.search.beginCalldate, "yyyyMMdd");
        params.endCalldate = format(this.search.endCalldate, "yyyyMMdd");
        params.beginCallhour = this.search.beginCallhour;
        params.endCallhour = this.search.endCallhour;
        params.groupBy = this.search.groupBy;
        params.page = this.search.page;
        params.size = this.search.size;
        params.province = this.search.provincevalue;
        params.city = this.search.cityvalue;
        params.cid = this.search.cid.join(",");
        params.prjid_local = this.search.prjid_local.join(",");
        params.uid = this.search.uservalue;
        params.modelname = this.search.modelvalue;
        return ajax.request({
          method:'post',
          url:'reason ',
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
          },
          transformRequest: [function(params) {      //在请求之前对data传参进行格式转换
            params = Qs.stringify(params)
            return params
          }],
          data:params
        }).then((res)=>{
          this.list.data=res.data.list;
          this.list.totalCount=res.data.totalCount;
          this.list.totalPage=res.data.totalPage;
        }).catch((error)=>{
          console.log(error)
        })
      },
      dialog_batch(type){
        if(type == 'modelname'){
          this.dataType = this.modelData;
          this.dataId = '1'
        }else if(type == 'province'){
          this.dataType = this.provinceData;
          this.dataId = '2'
        }else if(type == 'city'){
          this.dataType = this.cityData;
          this.dataId = '3'
        }else if(type == 'user'){
          this.dataType = this.userData;
          this.dataId = '4'
        }
        this.popup_show = true;
      },
      save() {
        this.popup_show = false;
        if(this.dataId == '1'){
          if(this.$refs.getDataName.complete()[0] != ''){
            this.search.modelname = this.$refs.getDataName.complete()[0].join(",");
            this.search.modelvalue = this.$refs.getDataName.complete()[1].join(",");
          }else{
            this.search.modelname = '';
            this.search.modelvalue = '0';
          }
        }else if(this.dataId == '2'){
          if(this.$refs.getDataName.complete()[0] != ''){
            this.search.province = this.$refs.getDataName.complete()[0].join(",");
            this.search.provincevalue = this.$refs.getDataName.complete()[1].join(",");
          }else{
            this.search.province = '';
            this.search.provincevalue = '0';
          }
        }else if(this.dataId == '3'){
          if(this.$refs.getDataName.complete()[0] != ''){
            this.search.city = this.$refs.getDataName.complete()[0].join(",");
            this.search.cityvalue = this.$refs.getDataName.complete()[1].join(",");
          }else{
            this.search.city = '';
            this.search.cityvalue = '0';
          }
        }else if(this.dataId == '4'){
          if(this.$refs.getDataName.complete()[0] != ''){
            this.search.umemo = this.$refs.getDataName.complete()[0].join(",");
            this.search.uservalue = this.$refs.getDataName.complete()[1].join(",");
          }else{
            this.search.umemo = '';
            this.search.uservalue = '0';
          }
        }
      },
      //分组条件选择
      checkedChange(type, val){
        var groupBy = this.search.groupBy.split(",");
        if(val){
          var newArr = [type];
          for(var i=0;i<groupBy.length;i++){
            if(newArr.indexOf(groupBy[i]) == -1){
              newArr.push(groupBy[i]);
            }
          }
          this.search.groupBy = newArr.join(",")
        }else{
          var index = groupBy.indexOf(type);
          groupBy.splice(index,1);
          if(groupBy.length == 0){
            this.search.groupBy = 'true';
          }else{
            this.search.groupBy = groupBy.join(",");
            var reg=/,$/g;
            this.search.groupBy=this.search.groupBy.replace(reg,"");
          }
          
        }
        
        this.getSeatList();
      },
      //处理外乎团队的数据
      formatCorp(row){
        for(var i=0;i<this.teamData.length;i++){
          if(row.cid == this.teamData[i].id){
            return row.crop = this.teamData[i].memo;
          }
        }
      },
      //处理品牌的数据
      formatProj(row){
        var projdata = this.projData;
        projdata = projdata.slice(1);
        for(var i=0;i<projdata.length;i++){
          if(row.prjid_local == projdata[i].id){
            return row.porj = projdata[i].memo;
          }
        }
      },
      //处理数据批次的数据
      formatModel(row){
        for(var i=0;i<this.modelData.length;i++){
          if(row.modelname == this.modelData[i].id){
            return row.modelName = this.modelData[i].memo;
          }
        }
      },
      //处理坐席姓名数据
      formatUser(row){
        for(var i=0;i<this.userData.length;i++){
          if(row.uid == this.userData[i].id){
            return row.user = this.userData[i].memo;
          }
        }
      },
      //处理省份的数据
      formatProvince(row){
        for(var i=0;i<this.provinceData.length;i++){
          if(row.province == this.provinceData[i].id){
            return row.provincename = this.provinceData[i].memo;
          }
        }
      },
      //处理城市的数据
      formatCity(row){
        for(var i=0;i<this.cityData.length;i++){
          if(row.city == this.cityData[i].id){
            return row.cityname = this.cityData[i].memo;
          }
        }
      },
      //分页
      change_data(index,size){
        this.search.page=index;
        this.search.size=size;
        this.getSeatList();
      }
    }
  }
</script>

<style scoped>
  @import '../../assets/css/pages/main.css';
  .quality{
    padding-left: 15px;
  }
  .quality .el-input{
    width: 150px;
  }
  .main-search .search-space{
    margin-right: 40px;
  }
  .item-right{
    display: inline-block;
  }
  .el-select-dropdown__wrap ul li{
    padding-left: 20px !important;
    display: block;
  }
</style>



